.BaAppBar {
    background-color: #ffffff !important;
    padding-top: 5px;
}

.BaAppBar .MuiTab-root {
    color: #0066c0;
    text-decoration: none;
}

.BaAppBar .MuiTab-root:hover {
    text-decoration: underline;
    color: #c45500;
}

.BaAppBar .MuiTabs-indicator {
    display: none;
}

.BaAppBar .MuiTab-root {
    border: 1px solid #999999;
    border-left: none;
    border-bottom: none;
    margin-top: 3px;
    background: #F5F5F5;
    outline: none;
}

.BaAppBar .Mui-selected {
    margin-top: 0px;
    border-top: 3px solid #c45500;
    background: #ffffff;
    padding: 5px 20px 15px 20px;
    color: #c45500;
    font-weight: bold;
    text-decoration: underline;
}